import Vue from 'vue'
import Router from 'vue-router'
import Axios from 'axios'
Vue.use(Router);
Vue.prototype.$http = Axios;


export default new Router({
  mode:'history',
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: (resolve) => require(['@/components/home'], resolve)
    },
    {
      path: '/about',
      name: 'About',
      component: (resolve) => require(['@/components/about'], resolve),
      children:[
        {
          path: '/about/about_head',
          name: 'AboutHead',
          component: (resolve) => require(['@/components/abouts/about_head'], resolve)
        },{
          path: '/about/about_body',
          name: 'AboutBody',
          component: (resolve) => require(['@/components/abouts/about_body'], resolve)
        },{
          path: '/about/about_body2',
          name: 'AboutBody',
          component: (resolve) => require(['@/components/abouts/about_body2'], resolve)
        },{
          path: '/about/about_footer',
          name: 'AboutFooter',
          component: (resolve) => require(['@/components/abouts/about_footer'], resolve)
        },{
          path: '/',
          component: (resolve) => require(['@/components/abouts/about_head'], resolve)
        }
      ]
    },
    {
      path: '/web',
      name: 'Web',
      component: (resolve) => require(['@/components/web'], resolve)
    },
    {
      path: '/python',
      name: 'Python',
      component: (resolve) => require(['@/components/python'], resolve)
    },
    {
      path: '/nodejs',
      name: 'Nodejs',
      component: (resolve) => require(['@/components/nodejs'], resolve)
    },
    {
      path: '/hobby',
      name: 'Hobby',
      component: (resolve) => require(['@/components/hobby'], resolve)
    },
    {
      path: '*',
      component: (resolve) => require(['@/components/home'], resolve)
    },
  ]
});



